@font-face {
    font-family: 'Helvetica-55';
    src: url('../fonts/helvetica-55/helvetica-neue-lt-geo-55-roman.eot');
    /* IE9 Compat Modes */
    src: url('../fonts/helvetica-55/helvetica-neue-lt-geo-55-roman.eot') format('embedded-opentype'), /* IE6-IE8 */
    url('../fonts/helvetica-55/helvetica-neue-lt-geo-55-roman.woff2') format('woff2'), /* Super Modern Browsers */
    url('../fonts/helvetica-55/helvetica-neue-lt-geo-55-roman.woff') format('woff'), /* Pretty Modern Browsers */
    url('../fonts/helvetica-55/helvetica-neue-lt-geo-55-roman.ttf') format('truetype'), /* Safari, Android, iOS */
    url('../fonts/helvetica-55/helvetica-neue-lt-geo-55-roman.svg') format('svg');
    /* Legacy iOS */
  }
